"use strict";

define(["text!./view.html", "knockout", "app/data-context-blog", "app/data-context-code", "underscore", 'lib/knockout-bindings-ext', 'lib/knockout-bindings-datetime', 'lib/knockout-bindings-highlight'], function (template, ko, dataCtxBlog, dataCtxCode, _) {

    return {
        viewModel: ViewModel,
        template: template
    };

    function ViewModel() {

        var self = this;

        this.blogs = ko.observableArray([]);
        this.blogsError = ko.observable(false);
        this.blogsLoading = ko.observable(true);
        this.hasBlogs = ko.pureComputed(function () {

            return this.blogs().length > 0;
        }, this);

        this.repos = ko.observableArray([]);
        this.reposError = ko.observable(false);
        this.reposLoading = ko.observable(true);
        this.hasRepos = ko.pureComputed(function () {

            return this.repos().length > 0;
        }, this);

        dataCtxBlog.blogs.getSummaries(null, 4).then(self.blogs, function (err) {

            self.blogsError(true);

            console.log('Error occured trying to get the articles', err);
        }).then(function () {

            self.blogsLoading(false);
        });

        dataCtxCode.repos.get(9).then(function (repos) {

            self.repos(_(repos).first(5));
            self.reposLoading(false);
        }, function (err) {

            self.reposError(true);
            self.reposLoading(false);

            console.log('Error occured trying to get the repos', err);
        });
    }
});